.main-nav {
    display: flex;
    justify-content: space-around;
    /* You can use 'space-between' or other values based on your preference */
    align-items: center;
    /* Align items vertically in the center if needed */
}

.main-nav ul {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
}

.main-nav li {
    margin: 0;
    padding: 0;
}

.main-nav a {
    text-decoration: none;
    color: #000;
    /* Set the desired color for your links */
    padding: 10px;
    /* Add padding to your links for better spacing */
    border-bottom: 2px solid transparent;
    /* Add a transparent border initially */
}

.main-nav a:hover {
    border-bottom: 2px solid #000;
    /* Change the border color on hover */
}